body {
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  display: flex;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 100%;
  width: 80vmin;
  height: 80vmin;
  position: relative;
}

.stopwatch__face {
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  display: flex;
  position: relative;
}

.stopwatch__hand {
  transform-origin: bottom;
  position: absolute;
}

.stopwatch__hand--minute {
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s steps(60, end) infinite rotate-minute-keyframe;
  top: calc(50% - 20vmin);
}

.stopwatch__hand--second {
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite rotate-second-keyframe;
  top: calc(50% - 38vmin);
}

.stopwatch__center {
  background-color: #f6a603;
  border-radius: 100%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
  top: calc(50% - 2.5vmin);
  left: calc(50% - 2.5vmin);
}

.stopwatch--speed-up .stopwatch__hand--minute {
  animation-duration: 600s;
}

.stopwatch--speed-up .stopwatch__hand--second {
  animation-duration: 10s;
}

@keyframes rotate-minute-keyframe {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

@keyframes rotate-second-keyframe {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

